import React from "react";
import classNames from "classnames";
import PropTypes from "prop-types";
import Button from "../Button";

function PizzaBlock({
  id,
  price,
  name,
  imageUrl,
  types,
  sizes,
  OnAddPizza,
  addedCount,
}) {
  const typesNames = ["—Ç–æ–Ω–∫–æ–µ", "—Ç—Ä–∞–¥–∏—Ü–∏–æ–Ω–Ω–æ–µ"];
  const sizesNames = [26, 30, 40];
  const [activeType, setActiveType] = React.useState(types[0]);
  const [activeSize, setActiveSize] = React.useState(sizes[0]);

  const onSelectType = (type) => {
    setActiveType(type);
  };

  const onSelectSize = (size) => {
    setActiveSize(size);
  };

  const handleAddPizza = () => {
    const pizza = {
      id,
      name,
      imageUrl,
      price,
      size: sizesNames[sizesNames.indexOf(activeSize)],
      type: typesNames[activeType],
    };
    OnAddPizza(pizza);
  };

  return (
    <div className="pizza-block">
      <img className="pizza-block__image" src={imageUrl} alt="Pizza" />
      <h4 className="pizza-block__title">{name}</h4>
      <div className="pizza-block__selector">
        <ul>
          {typesNames.map((type, index) => (
            <li
              onClick={() => onSelectType(index)}
              key={`${type}_${index}`}
              className={classNames({
                active: activeType === index,
                disabled: !types || !types.includes(index),
              })}
            >
              {type}
            </li>
          ))}
        </ul>
        <ul>
          {sizesNames.map((size, index) => (
            <li
              key={`${size}_${index}`}
              onClick={() => onSelectSize(size)}
              className={classNames({
                active: size === activeSize,
                disabled: !sizes.includes(size),
              })}
            >
              {size} —Å–º.
            </li>
          ))}
        </ul>
      </div>
      <div className="pizza-block__bottom">
        <div className="pizza-block__price">–æ—Ç {price} ‚ÇΩ</div>
        <Button onClick={handleAddPizza} className="button--add" outline>
          <svg
            width="12"
            height="12"
            viewBox="0 0 12 12"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
              fill="white"
            />
          </svg>
          <span>–î–æ–±–∞–≤–∏—Ç—å</span>
          {Boolean(addedCount) && <i>{addedCount}</i>}
        </Button>
      </div>
    </div>
  );
}

PizzaBlock.propTypes = {
  name: PropTypes.string.isRequired,
  imageUrl: PropTypes.string.isRequired,
  price: PropTypes.number.isRequired,
  types: PropTypes.arrayOf(PropTypes.number).isRequired,
  sizes: PropTypes.arrayOf(PropTypes.number).isRequired,
};

PizzaBlock.defaultProps = {
  // –ï—Å–ª–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –Ω–∏—á–µ–≥–æ –Ω–µ –ø–æ–ª—É—á–∏–ª —Ç–æ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å —ç—Ç–∏ –∑–Ω–∞—á–µ–Ω–∏—è –¥–ª—è props
  name: "default",
  types: [],
};

export default PizzaBlock;
